import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import i18n from "i18n/i18n";
import queryString from "query-string";
import { Formik, withFormik } from "formik";
import * as Yup from "yup";
import { useHistory, useLocation } from "react-router-dom";

import "./SubjectEdit.css";

import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import PageHeader from "./components/PageHeader/PageHeader";
import subjectApi from "api/common/subject";
import {
  FaCalculator,
  FaCode,
  FaExchangeAlt,
  FaPlus,
  FaSync,
  FaTextWidth,
  FaTrash,
  FaUserAlt,
  FaUsersCog,
} from "react-icons/fa";
import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
} from "reactstrap";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import { RoutesString } from "pages/routesString";
import { toast } from "react-toastify";

const SubjectEdit = function ({ values, handleChange, resetForm, errors }) {
  const { t } = useTranslation();

  const history = useHistory();

  const location = useLocation();

  const [id, setId] = useState(null);
  const [isUpdatePage, setIsUpdatePage] = useState(false);
  const [initialValues, setInitialValues] = useState({
    ma: "",
    tenTiengAnh: "",
    tenTiengViet: "",
    soTinChi: "",
    soTietLyThuyet: "",
    soTietThucHanh: "",
    soTietTuHoc: "",
  });
  const [notification, setNotification] = useState({
    color: "danger",
    isOpen: false,
    messages: [],
  });
  const [isLoading, setIsLoading] = useState(false);

  useEffect(async () => {
    const parsed = queryString.parse(window.location.search);
    if (parsed.id) {
      setId(parsed.id);
      setIsUpdatePage(true);
      updateForm(parsed.id);
    }
  }, [location]);

  const updateForm = async function (id) {
    setIsLoading(true);
    try {
      const res = await subjectApi.getById(id);
      setInitialValues({
        ma: res.ma,
        tenTiengViet: res.tenTiengViet,
        tenTiengAnh: res.tenTiengAnh,
        soTinChi: res.soTinChi,
        soTietLyThuyet: res.soTietLyThuyet,
        soTietThucHanh: res.soTietThucHanh,
        soTietTuHoc: res.soTietTuHoc,
      });
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    }
    setIsLoading(false);
  };

  const handleEditOnClick = async function (values) {
    try {
      if (isUpdatePage) {
        const body = {
          ...values,
        };
        const res = await subjectApi.putById(id, body);
        if (res) {
          toast.success(t("admin.UPDATE_SUCCESS"));
          updateForm(id);
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      } else {
        const body = {
          ...values,
        };
        const res = await subjectApi.post(body);
        if (res) {
          toast.success(t("admin.ADD_SUCCESS"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    } catch (err) {
      if (err?.response) {
        if (err?.response?.status === 403 || err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
            toast.error(t("ER_DUP_ENTRY"));
          }
          else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          }
          else {
            toast.error(t("RESPONSE_MESSAGE500"));
          }
        }
      }
    }
  };

  const handleDeleteOnClick = async function (e) {
    e.preventDefault();
    try {
      const res = await subjectApi.deleteById(id);
      if (res.status === 200) {
        toast.success(t("admin.common.DELETED"));
        history.push(`${RoutesString.ADMIN_SUBJECT_LIST}`);
      }
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || err?.response?.status === 401) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE500"));
          }
        }
      }
    }
  };

  return (
    <div className="page">
      {isLoading && <LoadingBar />}

      <Breadcrumbs
        currentItem={
          isUpdatePage
            ? t("admin.common.UPDATE")
            : t("admin.common.ADD")
        }
        items={[
          {
            href: RoutesString.ADMIN_SUBJECT_LIST,
            title: t("admin.subject_list.LIST"),
          },
        ]}
      />

      <PageHeader
        headerTitle={
          isUpdatePage
            ? t("admin.common.UPDATE")
            : t("admin.common.ADD")
        }
      />

      <div className="page-content">
        <Formik
          initialValues={initialValues}
          enableReinitialize={true}
          validationSchema={Yup.object().shape({
            soTinChi: Yup.number()
              .required(i18n.t("REQUIRED_FIELD_WARNING"))
              .integer("Total of credits must be a number")
              .min(0, i18n.t("admin.POSITIVE_WARNING")),
            soTietLyThuyet: Yup.number()
              .required(i18n.t("REQUIRED_FIELD_WARNING"))
              .integer("Number of theoretical periods must be a number")
              .min(0, i18n.t("admin.POSITIVE_WARNING")),
            soTietThucHanh: Yup.number()
              .required(i18n.t("REQUIRED_FIELD_WARNING"))
              .integer("Number of practice periods must be a number")
              .min(0, i18n.t("admin.POSITIVE_WARNING")),
            soTietTuHoc: Yup.number()
              .required(i18n.t("REQUIRED_FIELD_WARNING"))
              .integer("Number of self-study periods must be a number")
              .min(0, i18n.t("admin.POSITIVE_WARNING")),
            tenTiengViet: Yup.string().required(
              i18n.t("REQUIRED_FIELD_WARNING")
            ),
            tenTiengAnh: Yup.string().required(
              i18n.t("REQUIRED_FIELD_WARNING")
            ),
          })}
        >
          {({
            values,
            errors,
            dirty,
            isValid,
            handleChange,
            handleReset,
            setFieldValue,
          }) => (
            <Form>
              <Notification
                color={notification.color}
                isOpen={notification.isOpen}
                messages={notification.messages}
              />
              <FormGroup className="row">
                <Label for="value" className="col-lg-3">
                  {t("admin.subject_list.CODE")} <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaCode />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="text"
                      name="ma"
                      placeholder={t("admin.subject_edit.CODE_PLACEHOLDER")}
                      value={values.ma}
                      onChange={handleChange}
                      invalid={errors.ma}
                      readOnly={isUpdatePage}
                    />
                  </InputGroup>
                  {errors.ma && <FormText color="danger">{errors.ma}</FormText>}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="name" className="col-lg-3">
                  {t("admin.subject_list.VN_NAME")} <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaTextWidth />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="text"
                      name="tenTiengViet"
                      placeholder={t("admin.subject_edit.VN_NAME_PLACEHOLDER")}
                      value={values.tenTiengViet}
                      onChange={handleChange}
                      invalid={errors.tenTiengViet}
                    />
                  </InputGroup>
                  {errors.tenTiengViet && (
                    <FormText color="danger">{errors.tenTiengViet}</FormText>
                  )}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="name" className="col-lg-3">
                  {t("admin.subject_list.EN_NAME")} <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaTextWidth />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="text"
                      name="tenTiengAnh"
                      placeholder={t("admin.subject_edit.EN_NAME_PLACEHOLDER")}
                      value={values.tenTiengAnh !== 'NULL' ? values.tenTiengAnh : ''}
                      onChange={handleChange}
                      invalid={errors.tenTiengAnh}
                    />
                  </InputGroup>
                  {errors.tenTiengAnh && (
                    <FormText color="danger">{errors.tenTiengAnh}</FormText>
                  )}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="name" className="col-lg-3">
                  {t("admin.subject_list.TOTAL_CREDIT")} <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaCalculator />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="number"
                      name="soTinChi"
                      placeholder={t(
                        "admin.subject_edit.TOTAL_CREDIT_PLACEHOLDER"
                      )}
                      value={values.soTinChi}
                      onChange={handleChange}
                      invalid={errors.soTinChi}
                    />
                  </InputGroup>
                  {errors.soTinChi && (
                    <FormText color="danger">{errors.soTinChi}</FormText>
                  )}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="name" className="col-lg-3">
                  {t("admin.subject_list.TOTAL_THEORY_PERIOD")} <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaCalculator />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="number"
                      name="soTietLyThuyet"
                      placeholder={t(
                        "admin.subject_edit.TOTAL_THEORY_PERIOD_PLACEHOLDER"
                      )}
                      value={values.soTietLyThuyet}
                      onChange={handleChange}
                      invalid={errors.soTietLyThuyet}
                    />
                  </InputGroup>
                  {errors.soTietLyThuyet && (
                    <FormText color="danger">{errors.soTietLyThuyet}</FormText>
                  )}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="name" className="col-lg-3">
                  {t("admin.subject_list.TOTAL_PRACTICE_PERIOD")}{" "}
                  <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaCalculator />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="number"
                      name="soTietThucHanh"
                      placeholder={t(
                        "admin.subject_edit.TOTAL_PRACTICE_PERIOD_PLACEHOLDER"
                      )}
                      value={values.soTietThucHanh}
                      onChange={handleChange}
                      invalid={errors.soTietThucHanh}
                    />
                  </InputGroup>
                  {errors.soTietThucHanh && (
                    <FormText color="danger">{errors.soTietThucHanh}</FormText>
                  )}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="name" className="col-lg-3">
                  {t("admin.subject_list.TOTAL_SELF_PERIOD")} <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaCalculator />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="number"
                      name="soTietTuHoc"
                      placeholder={t(
                        "admin.subject_edit.TOTAL_SELF_PERIOD_PLACEHOLDER"
                      )}
                      value={values.soTietTuHoc}
                      onChange={handleChange}
                      invalid={errors.soTietTuHoc}
                    />
                  </InputGroup>
                  {errors.soTietTuHoc && (
                    <FormText color="danger">{errors.soTietTuHoc}</FormText>
                  )}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup>
                <Button
                  color="success"
                  disabled={isUpdatePage ? !isValid : !(isValid && dirty)}
                  onClick={() =>
                    handleEditOnClick(
                      values,
                      isUpdatePage ? isValid : isValid && dirty
                    )
                  }
                >
                  {isUpdatePage ? (
                    <>
                      <FaExchangeAlt /> {t("admin.common.UPDATE")}
                    </>
                  ) : (
                    <>
                      <FaPlus /> {t("admin.common.ADD")}
                    </>
                  )}
                </Button>
                &emsp;
                {isUpdatePage && (
                  <>
                    <Button onClick={handleReset}>
                      <FaSync /> {t("admin.common.REFRESH")}
                    </Button>
                    &emsp;
                    <Button color="danger" onClick={handleDeleteOnClick}>
                      <FaTrash /> {t("admin.common.DELETE")}
                    </Button>
                  </>
                )}
              </FormGroup>
            </Form>
          )}
        </Formik>
      </div>
    </div>
  );
};

export default SubjectEdit;
